import { IconBrandLastfm, IconSearch } from "@tabler/icons-react";
import { Autocomplete, Burger, Group } from "@mantine/core";
import { useDisclosure } from "@mantine/hooks";
import classes from "~/styles/HeaderSearch.module.css";
import ColorSchemeToggle from "./ColorSchemeToggle";

const links = [
  { link: "/about", label: "Features" },
  { link: "/community", label: "Community" },
];

const HeaderSearch = () => {
  const [opened, { toggle }] = useDisclosure(false);

  const items = links.map((link) => (
    <a
      key={link.label}
      href={link.link}
      className={classes.link}
      onClick={(event) => event.preventDefault()}
    >
      {link.label}
    </a>
  ));

  return (
    <header className={classes.header}>
      <div
        className={classes.inner}
      // TODO: find a way to offset the search bar to center it with screen
      >
        <Group>
          <Burger opened={opened} onClick={toggle} size="sm" hiddenFrom="sm" />
          <IconBrandLastfm
            size={50}
            color="var(--mantine-color-white)"
            stroke={1.5}
          />
        </Group>

        <Autocomplete
          w={{ base: "30%", xs: "15%", sm: "20%", md: "30%" }}
          className={classes.search}
          placeholder="Search for Songs, Artists, or Genres"
          leftSection={<IconSearch size={20} stroke={1.5} />}
          // TODO: replace with music data search
          data={["TODO : Implement music search"]}
          visibleFrom="xs"
        />

        <Group>
          <Group ml={50} gap={5} className={classes.links} visibleFrom="sm">
            {items}
          </Group>
        </Group>

        <ColorSchemeToggle />
      </div>
    </header>
  );
};

export default HeaderSearch;

