"use client";

import {
  Box,
  Stack,
  Title,
  Text,
  Loader,
  Group,
  Badge,
  Tooltip,
} from "@mantine/core";
import {
  IconCheck,
  IconX,
  IconHeart,
  IconCalendar,
} from "@tabler/icons-react";
import { useState, useEffect } from "react";
import styles from "../dashboard.module.css";
import Image from "next/image";

interface CurrentTrack {
  name: string;
  artist: string;
  album: string;
  image?: string;
  url?: string;
  loved: boolean;
  playedAt?: string;
}

const PlayerCard = () => {
  const [track, setTrack] = useState<CurrentTrack | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchCurrentTrack = async () => {
      try {
        setLoading(true);
        const response = await fetch("/api/music/current");

        if (!response.ok) {
          throw new Error("Failed to fetch current track");
        }

        const data = (await response.json()) as {
          track: CurrentTrack | null;
        };
        setTrack(data.track);
      } catch (err) {
        setError(
          err instanceof Error ? err.message : "Failed to fetch track"
        );
        console.error("Error fetching track:", err);
      } finally {
        setLoading(false);
      }
    };

    fetchCurrentTrack();
  }, []);

  if (loading) {
    return (
      <Box className={styles.glassCard} style={{ maxWidth: "420px" }}>
        <Stack gap="lg" align="center" justify="center" h={500}>
          <Loader size="lg" />
          <Text c="dimmed">Loading track...</Text>
        </Stack>
      </Box>
    );
  }

  if (error || !track) {
    return (
      <Box className={styles.glassCard} style={{ maxWidth: "420px" }}>
        <Stack gap="lg" align="center" justify="center" h={500}>
          <Text c="red">{error || "No track data available"}</Text>
          <Text size="sm" c="dimmed">
            Make sure your Last.fm account is connected and has scrobbles
          </Text>
        </Stack>
      </Box>
    );
  }

  return (
    <Box className={styles.glassCard} style={{ maxWidth: "420px" }}>
      {/* Album Art */}
      <div className={styles.albumArt}>
        {track.image ? (
          <Image
            src={track.image}
            alt={`${track.name} by ${track.artist}`}
            width={400}
            height={400}
            priority
          />
        ) : (
          <div
            style={{
              width: "100%",
              height: "400px",
              background: "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              color: "white",
            }}
          >
            <Text>No album art</Text>
          </div>
        )}
      </div>

      {/* Track Info */}
      <Stack gap="xs" align="center">
        <div style={{ width: "100%", overflow: "hidden", textAlign: "center" }}>
          <Title order={2} size="h3" style={{ marginBottom: "4px", whiteSpace: "nowrap", overflow: "hidden", textOverflow: "ellipsis" }}>
            {track.name}
          </Title>
        </div>
        <Text size="sm" c="dimmed" truncate style={{ width: "100%" }}>
          {track.artist}
        </Text>
        <Text size="xs" c="dimmed">
          {track.album}
        </Text>
      </Stack>

      {/* Track Metadata */}
      <Group justify="center" mt="md" gap="xs">
        {track.loved && (
          <Tooltip label="Loved on Last.fm">
            <Badge
              leftSection={<IconHeart size={12} />}
              variant="filled"
              color="red"
            >
              Loved
            </Badge>
          </Tooltip>
        )}
        {track.playedAt && (
          <Tooltip label="Last played">
            <Badge leftSection={<IconCalendar size={12} />} variant="light">
              {track.playedAt}
            </Badge>
          </Tooltip>
        )}
      </Group>

      {/* Track Link */}
      {track.url && (
        <Group justify="center" mt="md">
          <a
            href={track.url}
            target="_blank"
            rel="noopener noreferrer"
            style={{ textDecoration: "none" }}
          >
            <Text size="sm" c="blue" component="span">
              View on Last.fm â†’
            </Text>
          </a>
        </Group>
      )}

      {/* Swipe Controls */}
      <div className={styles.swipeControls} style={{ marginTop: "auto" }}>
        <button className={`${styles.swipeButton} ${styles.rejectButton}`}>
          <IconX size={24} />
        </button>
        <button className={`${styles.swipeButton} ${styles.acceptButton}`}>
          <IconCheck size={24} />
        </button>
      </div>
    </Box>
  );
};

export default PlayerCard;
