import { SegmentedControl } from "@mantine/core";

export function GradientSegmentedControl({
	classNames,
	radius,
	size,
	data,
	value,
	onChange,
}) {
	return (
		<SegmentedControl
			classNames={{ ...classNames }}
			radius={radius}
			size={size}
			data={data}
			value={value}
			onChange={onChange}
		/>
	);
}
