"use client";

import { Stack, Text, ScrollArea, Box, Loader, Group } from "@mantine/core";
import { useState, useEffect } from "react";
import styles from "../dashboard.module.css";

interface Track {
  name: string;
  artist: string;
  playcount: number;
  image?: string;
  url?: string;
}

interface TopTracksResponse {
  period: string;
  limit: number;
  tracks: Track[];
}

const PlaylistStack = () => {
  const [tracks, setTracks] = useState<Track[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchTopTracks = async () => {
      try {
        setLoading(true);
        const response = await fetch("/api/music/top-tracks?limit=10&period=overall");

        if (!response.ok) {
          throw new Error("Failed to fetch top tracks");
        }

        const data = (await response.json()) as TopTracksResponse;
        setTracks(data.tracks);
      } catch (err) {
        setError(
          err instanceof Error ? err.message : "Failed to fetch tracks"
        );
        console.error("Error fetching top tracks:", err);
      } finally {
        setLoading(false);
      }
    };

    fetchTopTracks();
  }, []);

  return (
    <div className={styles.playlistContainer}>
      {/* Playlist Header */}
      <div className={styles.playlistHeader}>
        <Text className={styles.playlistTitle}>Top Tracks</Text>
      </div>

      {/* Songs List */}
      <ScrollArea className={styles.playlistScrollArea}>
        {loading ? (
          <Stack gap="sm" align="center" justify="center" p="md">
            <Loader size="sm" />
            <Text size="sm" c="dimmed">
              Loading tracks...
            </Text>
          </Stack>
        ) : error ? (
          <Stack gap="sm" align="center" justify="center" p="md">
            <Text size="sm" c="red">
              {error}
            </Text>
            <Text size="xs" c="dimmed">
              Make sure your Last.fm account is connected
            </Text>
          </Stack>
        ) : tracks.length === 0 ? (
          <Stack gap="sm" align="center" justify="center" p="md">
            <Text size="sm" c="dimmed">
              No tracks found
            </Text>
          </Stack>
        ) : (
          <Stack gap="sm" className={styles.songsList}>
            {tracks.map((track, index) => (
              <a
                key={`${track.artist}-${track.name}`}
                href={track.url}
                target="_blank"
                rel="noopener noreferrer"
                style={{ textDecoration: "none" }}
              >
                <button
                  className={styles.songItem}
                  title={`${track.name} by ${track.artist} (${track.playcount} plays)`}
                >
                  <div className={styles.songItemContent}>
                    <Group justify="space-between" w="100%" gap={0}>
                      <div style={{ flex: 1, minWidth: 0 }}>
                        <Text className={styles.songTitle} truncate>
                          {track.name}
                        </Text>
                        <Text className={styles.songArtist} truncate>
                          {track.artist}
                        </Text>
                      </div>
                      <Text
                        size="xs"
                        c="dimmed"
                        style={{ whiteSpace: "nowrap", marginLeft: "8px" }}
                      >
                        {track.playcount} plays
                      </Text>
                    </Group>
                  </div>
                </button>
              </a>
            ))}
          </Stack>
        )}
      </ScrollArea>
    </div>
  );
};

export default PlaylistStack;
